.about {
  padding: 0rem 1rem;
  padding-bottom: 3rem;

  &__image-container {
    display: none;
  }

  &__title-container {
    text-align: center;
    margin-bottom: 2rem;
  }

  &__card {
    padding: 2rem 1.5rem;
    box-shadow: 1px 3px 15px 1px rgba(0, 0, 0, 0.7);
    background-color: white;

    &-text {
      margin-bottom: 2rem;
      color: gray;
      letter-spacing: 1px;
      line-height: 1.3rem;
    }

    &-subtext {
      h2 {
        font-size: x-large;
        font-weight: 700;
        margin-bottom: 1rem;
        color: $primary;
      }
      &-item {
        display: flex;
        align-items: center;
        margin-bottom: 1rem;
        div {
          height: 12px;
          width: 12px;
          margin-right: 1rem;
          margin-left: 0.5rem;
          border-radius: 50%;
          background-color: $primary;
        }
        p {
          color: gray;
          font-size: small;
          letter-spacing: 1px;
        }
      }
    }

    &-actions {
      margin-top: 2rem;
      display: flex;
      flex-direction: column;
      gap: 1rem;
      align-items: center;
    }
  }
}

@media screen and (min-width: 768px) {
  .about {
    display: flex;
    &__image-container {
      display: flex;
      flex: 1;
    }

    &__text-container {
      flex: 1;
    }
  }
}
