* {
  font-family: Helvetica, Arial, sans-serif;
  padding: 0px;
  margin: 0px;
}

html,
body {
  height: 100vh;
  width: 100%;
  background-image: url("../../assets/backgraund-image.png");
  background-repeat: no-repeat;
  background-size: cover;
}

img {
  max-width: 100%;
}

a:link,
a:visited,
a:active {
  text-decoration: none;
  color: black;
}

main {
  width: 100%;
}

.shadow-small {
  box-shadow: 1px 3px 15px 1px rgba(0, 0, 0, 0.7);
}

.btn {
  padding: 0.6rem 1.2rem;
  border-radius: 1rem;
  border: none;
  box-shadow: 1px 3px 15px 1px rgba(0, 0, 0, 0.7);
  font-size: 0.9rem;
  letter-spacing: 1px;
  cursor: pointer;
  transition: 0.3s ease;

  &:hover {
    transform: scale(1.1);
    transition: 0.3s ease;
  }

  &:active {
    box-shadow: none;
  }

  &-primary {
    background-color: $primary;
    color: white;
  }

  &-outline {
    border: 2px solid $primary;
    color: $primary;
  }
}

.social-link {
  border: none;
  border-radius: 1rem;
  padding: 0.1rem;
  background-color: transparent;
  cursor: pointer;
  transition: 0.3s ease;
  &:hover {
    transform: scale(1.2);
    box-shadow: 1px 3px 12px 1px rgba(0, 0, 0, 0.1);
  }
}

.page {
  &-title {
    text-align: center;
    font-family: "Courgette", cursive;
    color: white;
    letter-spacing: 1px;
  }

  &-subtitle {
    font-family: "Courgette", cursive;
    letter-spacing: 1px;
    font-size: larger;
    font-weight: 600;
  }
}
