.proyect {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__title-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    margin-bottom: 2rem;
  }

  &__card {
    border-radius: 1rem;
    background-color: white;
    width: 90%;
    display: flex;
    gap: 1rem;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    margin-bottom: 2rem;

    &-title {
      align-self: flex-start;
      display: flex;
      flex-direction: column;
      gap: 1rem;
      h2 {
        color: $primary;
      }
      p {
        color: gray;
      }
    }

    &-images-container {
      width: 300px;
      height: 300px;

      .carrusel-image {
        width: 300px;
        height: 300px;
        img {
          border-radius: 2rem;
          height: 100%;
          width: auto;
        }
      }
    }

    &-footer {
      display: flex;
      align-items: center;
      a {
        color: $primary;
        font-weight: 600;
      }
    }
  }
}
@media screen and (min-width: 768px) {
  .proyect {
    &__card {
      width: 70%;
    }
  }
}
