.navbar {
  position: relative;
  display: flex;
  z-index: 100;
  .header-toggle {
    display: flex;
    align-items: center;
    background-color: $primary;
    width: 100vw;
    height: 15vh;
    padding: 1rem 2rem;
    button {
      background-color: transparent;
      border: none;
      cursor: pointer;

      &:hover {
        transform: scale(1.2);
      }
    }
    p {
      margin-left: 2rem;
      font-size: xx-large;
      color: $tertiary;
      font-weight: 700;
      font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
        "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
    }
  }
  .nav-links {
    position: absolute;
    background-color: $primary;
    left: -100vw;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-evenly;
    height: 100vh;
    width: 100vw;
    transition: 0.3s ease-in;

    .toggle {
      align-self: flex-start;
      margin-left: 5rem;
      button {
        font-size: 2rem;
        font-weight: 700;
        background-color: transparent;
        color: $tertiary;
        border: none;
        padding: 0.3rem 0.5rem;
        cursor: pointer;
      }
    }

    li {
      list-style: none;
    }

    a {
      position: relative;
      color: $tertiary;
      font-size: large;
      font-weight: 600;
      letter-spacing: 2px;
      padding: 0.5rem 1rem;

      &::before,
      &::after {
        content: "";
        position: absolute;
        width: 0%;
        height: 4px;
        background-color: $tertiary;
        transition: 0.3s;
      }

      &::before {
        left: 0px;
        top: -5px;
      }

      &::after {
        right: 0px;
        bottom: -5px;
      }

      &:hover::after,
      &:hover::before {
        width: 100%;
      }
    }
  }
  .nav-links-active {
    left: 0px;
    transition: 0.3s ease-in;
  }

  .link-active {
    border-top: 4px solid $tertiary;
    border-bottom: 4px solid $tertiary;
    // padding-top: 10px;
  }
}

@media screen and (min-width: 768px) {
  .navbar {
    .header-toggle {
      flex: 1;
      button {
        display: none;
      }
    }
    .nav-links {
      position: relative;
      display: flex;
      flex-direction: row;
      left: 0px;
      flex: 5;
      height: 15vh;
      align-items: center;
      justify-content: space-between;
      padding: 1rem 2rem;

      .toggle {
        display: none;
      }
    }
  }
}
