.contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding-bottom: 2rem;

  &__title-container {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }

  &__card {
    display: flex;
    flex-direction: column;
    width: 90%;
    border-radius: 1rem;
    padding: 2rem 0.5rem 0.5rem 1rem;
    box-shadow: 1px 3px 15px 1px rgba(0, 0, 0, 0.7);
    gap: 1rem;
    background-color: $primary;
    &-social {
      display: flex;
      gap: 1rem;
    }
    &-content {
      display: flex;
    }

    &-image {
      display: none;
    }

    &-form {
      padding: 1rem;
      width: 100%;
      min-height: 400px;
      background-color: white;
      border-radius: 1rem;
      display: flex;
      flex-direction: column;
      justify-content: space-around;

      &-control {
        position: relative;
        display: flex;
        flex-direction: column;
        label {
          color: $primary;
          font-size: larger;
          margin-bottom: 0.3rem;
        }

        input,
        textarea {
          border-radius: 1rem;
          border: 2px solid $primary;
          background-color: rgb(240, 239, 239);
          padding: 0.5rem 1rem;

          &:focus {
            outline: none;
          }
        }
        textarea {
          height: 5rem;
        }
      }

      &-error {
        position: absolute;
        bottom: -1.3rem;
        color: $accent;
      }

      button {
        align-self: center;
        width: 10rem;
      }
    }
  }
}

@media screen and (min-width: 768px) {
  .contact {
    &__card {
      &-image {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        img {
          max-height: 400px;
        }
      }

      &-form {
        flex: 1;
      }
    }
  }
}
