.home__container {
  margin-top: 1rem;
  padding-bottom: 2rem;

  .home__image-container {
    display: flex;
    justify-content: center;
    img {
      border: 1px solid #ddd;
      border-radius: 50%;
      padding: 5px;
      transition: 0.5s ease-out;
      &:hover {
        transform: scale(1.1);
        transition: 0.5s ease-in;
      }
    }
  }

  .home__social-container {
    margin-top: 1rem;
    display: flex;
    justify-content: center;
    gap: 1rem;
  }

  .home__name {
    display: flex;
    justify-content: center;
    p {
      font-size: x-large;
      font-weight: 500;
      color: $accent;
    }
  }

  .home__resume-container {
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    padding-left: 2rem;
    padding-right: 2rem;
    p {
      &:first-child {
        font-size: xx-large;
        font-weight: 600;
        letter-spacing: 2px;
        text-align: center;
      }
      &:last-child {
        text-align: center;
        font-size: larger;
      }
    }
  }

  .home_action-container {
    display: flex;
    justify-content: space-around;
    margin-top: 2rem;
  }
}

@media screen and (min-width: 768px) {
  .home__container {
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;

    .home__image-container {
      flex: 1;
    }

    .home_text-container {
      flex: 1;
    }
  }
}
